
import { defineComponent, PropType } from 'vue'
import { currentUser } from '@/router'
import { passivDetails, passivAmount, maxEnergyCell } from '@/global'
import * as type from '@/types'
import * as music from '@/music'
export default defineComponent({
    setup() {
        currentUser
        return {
            passivDetails,
            passivAmount,
            maxEnergyCell,
        }
    },
    data() {
        return {
            player: {} as type.Player,
            user: currentUser,
            dataLoad: false,
        }
    },
    props: {
        playerProp: {
            type: Object as PropType<type.Player>,
            required: true,
        },
    },

    mounted() {
        this.player = this.playerProp
        this.dataLoad = true
        this.buttonSound()
    },
    methods: {
        buyEnergyCell() {
            if (this.player.shop.currency <= 0 || this.player.shop.energyCell >= this.maxEnergyCell) return
            this.buttonSound()
            this.player.shop.currency--
            this.player.shop.energyCell++
        },
        buyEnergyCellx8() {
            for (let i = 0; i < 8; i++) {
                if (this.player.shop.currency <= 0 || this.player.shop.energyCell >= this.maxEnergyCell) return
                this.player.shop.currency--
                this.player.shop.energyCell++
            }
        },
        buttonSound() {
            music.ButtonSound(this.player.settings.effectVolume)
        },
    },
})
